<template>
    <div class="select-container">
        <vue-confirm-dialog></vue-confirm-dialog>
        <button v-on:click="getChap(1)">chap.1</button>
        <button v-on:click="getChap(2)">chap.2</button>
    </div>
</template>

<script>

export default {
    name: 'Select',
    components: {},
    methods: {
        getChap: function (num) {
            var backup = this.$store.state.getchapter.response,
                capselect = false,
                noselect = false,
                selectmsg = '진행하던 플레이가 있습니다. \n이어하시겠습니까?',
                noselectmsg = '챕터의 처음부터 진행하시겠습니까?'

            if(backup.length > 0) {
                // 진행하던 챕터가 있을 경우,
                capselect = confirm(selectmsg)
                
                if(capselect) {
                    // 이어하기
                    this.$router.push({name: 'stage'})
                } else {
                    // 이어하지않음
                    noselect = confirm(noselectmsg)
                }

                if(noselect) {
                    // 챕터의 처음부터 시작
                    this.$store.commit('countreset')
                } else {
                    // 다시 셀렉트 창으로
                    return false
                }
            }

            // 진행하던 챕터가 없을 경우
            if(num === 1) {
                this.$store.commit('getChapOne')
            } else if(num === 2) {
                this.$store.commit('getChapTwo')
            }

            this.$router.push({name: 'stage'})
        }
    },
    created(){}
}
</script>

